// Fonts
@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Quicksand:wght@500;700&display=swap');

.container {
  text-align: center;
  display: grid;
  grid-template-areas: "nav dashboard";
  grid-template-columns: 20vw 80vw;
  grid-template-rows: 100vh;
  font-family: 'Quicksand', sans-serif;


  #nav{
    grid-area: nav;
    border-right: 1px solid rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    padding-top: 10vh;

    h1{
      margin: 1rem auto 0 auto;
      font-family: 'Pacifico', sans-serif;   
      transition: all 0.4s;     
    }
    h1:hover{
      scale: 1.1;
    }
    h4{
      margin: 0;
      font-weight: bold;
    }

    #links{
      text-align: left;
      margin: 5vh auto 0;
      width: 60%;
      display: flex;
      flex-direction: column;
      gap: 1rem;    
      overflow: hidden;

      h3{
        margin: 0;
        cursor: pointer;
        display: flex;
        transition: all 0.5s;
      }
    }

    #socials{
      height: 10vh;
      margin: auto 0 0 0;
      padding-left: 2rem;
      display: flex;

      a{
        display: flex;
        margin: auto 1rem auto 0;

        img{
          height: 4vh;
          transition: all 0.5s;
        }
        img:hover{
          scale: 1.1;
        }
      }

      h4{
        margin: auto 2rem auto auto;
      }
    }
  }

  #dashboard{
    grid-area: dashboard;
    overflow: auto;
    padding: 2rem;
    line-height: 2rem;
    -webkit-column-count: 3;
    -webkit-column-gap:   1rem;
    -moz-column-count:    3;
    -moz-column-gap:      1rem;
    column-count:         3;
    column-gap:           1rem; 
    transition: all 1s;

    img{
      width: 25%;
      width: 100% !important;
      height: auto !important;
      transition: all 0.5s;
    }
    img:hover{
      scale: 1.05;
    }
  }
}

.link{
  // color: #18272F;
  position: relative;
  text-decoration: none;
}

.link::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  border-radius: 4px;
  background-color: #18272F;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.link:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

